import { Component, OnInit } from '@angular/core';
import { UserService } from '../../shared/data/user.service';
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  userId = '';
  user;
  details;
  timeLines;
  hobbies;
  userImage = '';

  constructor(public userService: UserService, private router: Router, private route: ActivatedRoute){
    //this.route.queryParams.subscribe(params => {this.userId = params['id'];});
    if(this.userId == ''){ this.userId = localStorage.getItem('currentUserId')};
    this.userService.getUser(this.userId, new Date().getTime()).then(data => {
      if(data['status'] == 'success'){
        this.user = data['user'][0];
        if(data['user'][0].profileImage != undefined && data['user'][0].profileImage != null && data['user'][0].profileImage != ''){
          this.userImage = data['user'][0].profileImage;
        }
        else{
          this.userImage = 'assets/img/default_image.png';
        }
        console.log(this.user);
      }
    });

    this.userService.getUserDetails(this.userId).then(data => {
      if(data['status'] == 'success'){
        this.details = data['details'];
        console.log(this.details);
      }
    });

    this.userService.getTimeLine(this.userId).then(data => {
      if(data['status'] == 'success'){
        this.timeLines = data['timeLine'];
        console.log(this.timeLines);
      }
    });
  }

  edituser(id){
    let val = ((id*5362)/12345);
    this.router.navigate(['/users/edit-user'],{queryParams:{id:window.btoa(val.toString()), source:'user'}});
  }

  renew(){
    this.router.navigate(['/pages/renewal']);
  }

  ngOnInit() {
  }
}
